import { css } from 'lit';

export const styles = css`[part=inactive]{background:var(--base-track-color)}[part=steps]{color:var(--track-step-color)}[part=fill]{background:var(--track-color)}[part=tick]{background:var(--tick-color)}[part~=thumb]{background:var(--thumb-color)}[part=thumb-label-inner]{color:var(--label-text-color);background:var(--label-background-color);padding:0 .5rem}[part=thumb-label-inner]::after{border-top:.625rem solid var(--label-background-color)}:host([disabled]) [part~=thumb],:host(:disabled) [part~=thumb]{background:var(--disabled-thumb-color)}:host([disabled]) [part=inactive],:host(:disabled) [part=inactive]{background:var(--disabled-base-track-color)}`;
